@use '../../../sass/mixin.scss' as mix;
@use '../../../sass/variable.scss' as var;

.contactData {
	@include mix.flex;
	flex-direction: column;
	border: 3px solid yellow;
	border-radius: 15px;
	padding: 10px;
	width: 100%;
	background-color: var.$dark-violet;
	animation-name: animationForm;
	animation-duration: 2s;
	position: relative;

	@keyframes animationForm {
		0% {
			transform: scale(0.2);
			right: -100%;
		}
		100% {
			transform: scale(1);
			right: 0;
		}
	}

	h4 {
		font-size: var.$size-large;
		color: white;
		font-weight: bold;
		padding: 10px;
	}

	form {
		@include mix.flex;
		flex-direction: column;
		gap: 10px;
		width: 100%;
	}
}

.formField {
	@include mix.flex;
	flex-direction: column;
	gap: 5px;
	width: 100%;
	position: relative;
}

.formFieldLabel {
	@include mix.flex;
	gap: 20px;
	padding: 20px;
	width: 100%;
	border-radius: 20px;
	position: relative;

	&:hover {
		border: 2px dashed white;
	}

	input {
		appearance: none;
		width: 100%;
		height: 100%;
		border-radius: 20px;
		position: absolute;
		top: 0;
		left: 0;

		&:checked {
			border: 5px dashed var.$violet;
			background-color: var.$violet;
		}
	}

	span {
		font-size: var.$size-large;
		z-index: 10;
	}
}

.formFieldNav {
	@include mix.flex;
	gap: 5px;
	width: 100%;

	button {
		padding: 5px;
		border: 1px solid white;
		border-radius: 10px;
		width: 100%;
		color: white;
		background-color: var.$violet;
		margin-top: 10px;
		font-size: var.$size-small;
	}
}
