@use '../../../sass/mixin.scss' as mix;
@use '../../../sass/variable.scss' as var;

.contactData {
	@include mix.flex;
	flex-direction: column;
	border: 3px solid yellow;
	border-radius: 15px;
	padding: 10px;
	width: 100%;
	background-color: var.$dark-violet;
	animation-name: animationForm;
	animation-duration: 2s;
	position: relative;

	@keyframes animationForm {
		0% {
			transform: scale(0.2);
			right: -100%;
		}
		100% {
			transform: scale(1);
			right: 0;
		}
	}

	h4 {
		font-size: var.$size-large;
		color: white;
		font-weight: bold;
		padding: 10px;
	}

	form {
		@include mix.flex;
		flex-direction: column;
		gap: 10px;
		width: 100%;
	}
}

.formField {
	@include mix.flex;
	flex-direction: column;gap: 5px;
	width: 100%;

	label {
		align-self: start;
		font-size: var.$size-small;
	}

	input {
		font-size: var.$size-small;
		padding: 5px;
		border: 1px solid white;
		border-radius: 10px;
		width: 100%;
		color: white;
		background-color: var.$dark-violet;
	}

	button {
		padding: 5px;
		border: 1px solid white;
		border-radius: 10px;
		width: 100%;
		color: white;
		background-color: var.$violet;
		margin-top: 10px;
		font-size: var.$size-small;
	}
}
