@use '../../sass/mixin.scss' as mix;
@use '../../sass/variable.scss' as var;

.wraper {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 100%;
	background-color: var.$violet;
	padding: 20px;
}

.content {
	@include mix.flex();
	flex-direction: column;
	gap: 20px;
	width: 100%;
	max-width: 600px;
	background-color: var.$blue-dark;
	color: white;
	margin: auto;
	padding: 20px;
	border-radius: 20px;
	overflow: hidden;

	h3 {
		font-size: var.$size-medium;

	}
}

.contactData {
	@include mix.flex;
	flex-direction: column;
	border: 3px solid yellow;
	border-radius: 15px;
	padding: 10px;
	width: 100%;
	background-color: var.$dark-violet;
	animation-name: animationForm;
	animation-duration: 2s;
	position: relative;

	@keyframes animationForm {
		0% {
			transform: scale(0.2);
			right: -100%;
		}
		100% {
			transform: scale(1);
			right: 0;
		}
	}

	h4 {
		font-size: var.$size-large;
		color: white;
		font-weight: bold;
		padding: 10px;
	}

	form {
		@include mix.flex;
		flex-direction: column;
		gap: 10px;
		width: 100%;
	}
}

.formField {
	@include mix.flex;
	flex-direction: column;gap: 5px;
	width: 100%;

	label {
		align-self: start;
		font-size: var.$size-small;
	}

	input {
		font-size: var.$size-small;
		padding: 5px;
		border: 1px solid white;
		border-radius: 10px;
		width: 100%;
		color: white;
		background-color: var.$dark-violet;
	}

	button {
		padding: 5px;
		border: 1px solid white;
		border-radius: 10px;
		width: 100%;
		color: white;
		background-color: var.$violet;
		margin-top: 10px;
		font-size: var.$size-small;
	}
}

.loadWrapper {
@include mix.flex;
position: fixed;
left: 0;
top: 0;
z-index: 100;
width: 100vw;
height: 100vh;
background-color: var.$violet;
opacity: 0.9;

	img {
		width: 100px;
		height: 100px;
		animation-name: loadRotate;
		animation-duration: 2s;
		animation-iteration-count: infinite;

		@keyframes loadRotate {
			0% {
				transform: rotate(0deg);
			}
			100% {
				transform: rotate(-360deg);
			}
		}
	}
	
	span {
		font-size: 40px;
		padding: 15px;
		border: 1px solid white;
		border-radius: 10px;
		color: white;
		background-color: var.$dark-violet;
		text-align: center;
	}
}

.backToSite {
	display: block;
	font-size: var.$size-small;
	width: 100%;
	padding: 15px;
	border: 1px solid white;
	border-radius: 10px;
	color: white;
	background-color: var.$dark-violet;
	text-align: center;
}